<template>
  <v-dialog v-model="isActive" max-width="500">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn v-bind="activatorProps" title="編輯" icon="mdi-pencil" size="small" variant="text"></v-btn>
    </template>

    <template v-slot:default="{ isActive }">
      <v-card class="review-border">
        <v-card-title class="d-flex justify-space-between align-center">
          <span class="text-blue">編輯角色</span>
          <v-btn icon variant="text" @click="cancele()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-text-field hide-details="auto" v-model="form.serialNumber" density="compact" label="角色編號"
            prepend-inner-icon="mdi-music-accidental-sharp" variant="solo" required class="mb-5">
            <template #label>
              角色編號
            </template>
          </v-text-field>
          <v-text-field hide-details="auto" v-model="form.name" density="compact" label="角色名稱"
            prepend-inner-icon="mdi-account" variant="solo" required class="mb-5">
            <template #label>
              角色名稱
            </template>
          </v-text-field>
          <v-textarea v-model="form.description" label="描述" density="compact" variant="solo" required
            placeholder="描述"></v-textarea>
          <v-card>
            <v-card-item>
              <v-label>功能</v-label>
              <functions ref="tcFunctions" @change="change" class="mt-2" />
            </v-card-item>
          </v-card>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn text="取消" variant="outlined" color="grey-darken-2" @click="cancele()"></v-btn>
          <v-btn text="確認" variant="outlined" color="primary" @click="submit()"></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script setup>
import { onMounted } from 'vue';
import { roles } from "@/requests";
import functions from "./functions.vue";
import { watch, nextTick } from "vue";
const props = defineProps({
  role: {
    type: Object,
    required: true,
    default: () => ({
      id: "",
      serialNumber: "",
      name: "",
      description: "",
    }),
  },
});

const form = ref({
  id: "",
  serialNumber: "",
  name: "",
  description: "",
})
const func = ref({
  menus: [],
  pages: [],
  operations: [],
  apis: [],
})
const tcFunctions = ref(null);
onMounted(async () => {
  form.value.id = props.role.id;
  form.value.serialNumber = props.role.serialNumber;
  form.value.name = props.role.name;
  form.value.description = props.role.description;
});

const isActive = ref(false);
const emit = defineEmits(['edit-role']);

function submit() {
  roles.update({ id: form.value.id }, form.value).then(() => {
    roles.updateFunctions({ id: form.value.id }, func.value).then(() => {

    });
  });
  isActive.value = false
}
function cancele() {
  form.value.id = props.role.id;
  form.value.serialNumber = props.role.serialNumber;
  form.value.name = props.role.name;
  form.value.description = props.role.description;
  isActive.value = false
}
const change = (list) => {
  Object.assign(func.value, list);
}
watch(
  () => isActive.value,
  async (val) => {
    if (val) {
      const [a, b] = await Promise.all([
        roles.get({ id: form.value.id }),
        roles.functions({ id: form.value.id })
      ]);

      Object.assign(form.value, a.data);
      Object.assign(func.value, b.data);

      await nextTick();
      if (tcFunctions.value) {
        tcFunctions.value.deselectAll();
        tcFunctions.value.selection([
          ...b.data.menus,
          ...b.data.pages,
          ...b.data.operations,
          ...b.data.apis
        ]);
      }
    }
  },
  { immediate: true }
);
</script>
<style scoped>
.review-border {
  padding: 15px;
}

.review-button-edit {
  border-radius: 100px !important;
  /* height: 28px !important; */
  width: 60px !important;
}

.review-button-circle {
  font-size: 24px;
}

.review-button-content {
  font-size: 12px;
  position: relative;
  top: -5px;
}
</style>