<template>
  <v-dialog v-model="isActive" max-width="500">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn v-bind="activatorProps" title="刪除" icon="mdi-trash-can" size="small" variant="text"></v-btn>
    </template>

    <template v-slot:default="{ isActive }">
      <v-card>
        <v-card-title class="d-flex justify-space-between align-center">
          <span class="text-blue"> </span>
          <v-btn icon variant="text" @click="cancele()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <div class="delete-info">
            <img src="@/assets/images/delete-alert-icon.png" alt="Delete Alert Icon" class="delete-alert" />
            <span>是否確認<span class="text-red">刪除</span>?</span>
          </div>
        </v-card-text>
        <v-card-actions class="d-flex justify-center mb-5">
          <v-btn text="取消" variant="outlined" color="grey-darken-2" @click="cancele()"></v-btn>
          <v-spacer style="flex: 0.25;"></v-spacer>
          <v-btn text="確認" variant="outlined" color="primary" @click="submitDelete()"></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script setup>
import { onMounted } from 'vue';
import { roles } from '@/requests';
const props = defineProps({
  role: {
    type: Object,
    required: true,
    default: () => ({
      id: "",
      number: "",
      text: "",
      info: "",
    }),
  },
});

const emit = defineEmits(['delete-role']);

const form = ref({
  id: "",
  number: "",
  text: "",
  info: "",
})
onMounted(() => {
  form.value.id = props.role.id;
});
const isActive = ref(false);
function submitDelete() {  
  roles.delete({ id: form.value.id }).then(() => {
    emit('delete-role', form.value.id);
    form.value.id = "";
    isActive.value = false;
  }).catch((error) => {
    alert("刪除失敗，請重新整理後再試一次");
    isActive.value = false;
  });
}
function cancele() {
  isActive.value = false
}
</script>
<style scoped>
.delete-info {
  font-size: 18px;
  margin: 0px 40px 0px 40px;
}

.delete-alert {
  vertical-align: middle;
  margin-right: 64px;
  width: 64px;
  height: 64px;
}
</style>