<template>
  <v-row class="mt-1">
    <v-col cols="4">
      <v-text-field label="搜尋" v-model="filterWords" density="compact" variant="outlined"></v-text-field>
    </v-col>
    <v-col cols="4"></v-col>
    <v-col cols="4" class="d-flex justify-end">
      <Create @add-to-list="onCreateRole"></Create>
    </v-col>
  </v-row>
  <v-card class="rounded-lg elevation-3">
    <v-data-table :headers="headers" :items="filterRows" density="comfortable" class="cyan-header">
      <template v-slot:item.edit="{ item }">
        <Edit :role="item" @edit-role="onEditRole"></Edit>
      </template>
      <template v-slot:item.delete="{ item }">
        <Delete :role="item" @delete-role="onRoleDeleted"></Delete>
      </template>
    </v-data-table>
  </v-card>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import axios from "@/axios";
import Tree from "./tree.vue";
import * as ss from "@/helpers/sessionStorage";
import Create from "./create.vue";
import Edit from "./edit.vue";
import Delete from "./delete.vue";
import { roles } from "@/requests";

const response = ref([]);
onMounted(() => {
  refreshList();
});

const filterWords = ref("");
const filterRows = computed(() => {
  return response.value.filter((item) => {
    const matchesFilterWords =
      !filterWords.value ||
      (item.serialNumber && item.serialNumber.includes(filterWords.value)) ||
      (item.name && item.name.includes(filterWords.value)) ||
      (item.description && item.description.includes(filterWords.value));
    return matchesFilterWords;
  });
});

const headers = ref([
  { title: "角色編號", key: "serialNumber" },
  { title: "角色名稱", key: "name" },
  { title: "描述", key: "description" },
  { title: "編輯", key: "edit", sortable: false },
  { title: "刪除", key: "delete", sortable: false },
]);

function onCreateRole() {
  refreshList();
}
function onEditRole() {
  refreshList();
}
function onRoleDeleted() {
  refreshList();
}
function refreshList() {
  roles.list().then((a) => {
    response.value = a.data;
  });
}
</script>
<style>
.cyan-header thead th {
  color: #3B9AB8;
  font-weight: bold;
}
</style>